
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
name: "login",
components:{
Toast,XInput
},
data:function () {
return {
account:"",
password:"",
showTip:false,
tipMessage:"",
showWarn:false,
warnMessage:""
}
},
methods:{
submit(){
var that=this;
fetch("/users/login",{
method: "POST",
headers: {
'Content-Type': 'application/x-www-form-urlencoded'
},
body: "account="+that.account+"&password="+that.password
}).then(function (e) {
return e.json();
}).then(function (e) {
if(e.message){
that.showWarn=true;
that.warnMessage="账号或密码错误";
}else{
localStorage.userFront=e[0].uid;
localStorage.userName=e[0].name;
localStorage.userPhone=e[0].phone;
that.showTip=true;
that.tipMessage="登录成功";
setTimeout(function () {
that.$router.go(-1)
},600)
}

})
},
register(){
this.$router.push("/register")
}
}
}



