
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: "set-pass",
components:{XInput, XButton,Group,Toast},
data:function () {
return {
oldpass: '',
password: '',
password2: '',
showTip: false,
tipMsg: "",
}
},
mounted(){
this.$emit('title',this.$route.query.title);
},
methods:{
submit(){
var that=this;
if(this.oldpass.trim()==""){
this.showTip=true;
this.tipMsg="旧密码不能为空";
}else if(this.oldpass.trim()==this.password.trim()){
this.showTip=true;
this.tipMsg="旧密码与新密码不能一致";
}else if(this.password.trim()=="" ||this.password2.trim()==""){
this.showTip=true;
this.tipMsg="新密码不能为空";
}else if(this.password.trim()!=this.password2.trim()){
this.showTip=true;
this.tipMsg="两次密码不一致";
}else{
fetch("/users/updatePass?oldpass="+this.oldpass+"&pass="+this.password+"&uid="+localStorage.getItem("userFront")).then(function (e) {
return e.text();
}).then(function (e) {
if(e=="ok"){
that.showTip=true;
that.tipMsg="修改成功";
setTimeout(function () {
that.$router.go(-1)
},600)
}else if(e=="err"){
that.showTip=true;
that.tipMsg="修改失败";
setTimeout(function () {
that.$router.go(-1)
},600)
}
})
}

}
}
}



