
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
props:["gid"],
name:'goods-info',
directives: {
TransferDom
},
components: {
XHeader,Toast,
Actionsheet,
XButton,
Previewer
},
data () {
return {
menus: {
menu1: '分享',
menu2: '复制链接'
},
buyMenus: {
menu1: '直接电话联系',
menu2: '直接购买'
},
showMenus: false,
showBuyMenus: false,
gData:[],
imgArr: [],
showTip:false,
tipMsg:"",
isCollect:false,
uid:localStorage.getItem("userFront"),
}
},
beforeRouteLeave(to,from,next){
if(this.gData.state==1){
to.meta.keepAlive = false; 
}
next();
},
mounted:function () {
var that=this;
fetch("/goods/query?gid="+this.$route.params.gid+"&uid="+this.uid).then(function (e) {
return e.json()
}).then(function (e) {
if(e[0].gphoto){
that.imgArr=e[0].gphoto.split(";")
}
if(e[0].collect){
that.isCollect=true;
}else{
that.isCollect=false;
}
if(e[0].uid==that.uid){
e[0].isMy=true;
}else{
e[0].isMy=false;
}
that.gData=e[0];
})
},
methods:{
chooseMenus(key){
console.log(key)
},
chooseBuyMenus(key){
if(this.uid) {
if (key == "menu1") {
this.$refs.callPhone.click();
} else if (key == "menu2") {
this.$router.push("/buy/" + this.$route.params.gid)
}
}else{
this.$router.push("/login")
}
},
edit(gid){
this.$router.push("/publish/"+gid)
},
collect(gid){
var that=this;
if(this.uid){
fetch("/goods/addCollect?gid="+gid+"&uid="+this.uid).then(function (e) {
return e.json()
}).then(function (e) {
if(e.msg=="ok"){
that.showTip=true;that.tipMsg="收藏成功";
that.isCollect=true;
that.gData.collect=e.colid;
}
})
}else{
this.$router.push("/login")
}

},
delcollect(colid){
var that=this;
fetch("/goods/delCollect?colid="+colid).then(function (e) {
return e.text()
}).then(function (e) {
if(e=="ok"){
that.showTip=true;that.tipMsg="已取消收藏"
that.isCollect=false;
}
})
}
}
}



