
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'

export default {
directives: {
TransferDom
},
name: "priceinfo",
components:{FormPreview,Confirm,Toast},
mounted(){
var that=this;
this.$emit("title",this.$route.query.title);

fetch("/users/select/"+that.uid).then(function (e) {
return e.json()
}).then(function (e) {
that.money=e[0].money
})

fetch("/users/priceInfo/"+that.uid).then(function (e) {
return e.json();
}).then(function (e) {
for(var i in e){
var arr=[{},{},{},{}]
arr[0].label="商品";
arr[1].label="收货人";
arr[2].label="收货地址";
arr[3].label="时间";
arr[0].value=e[i].gtitle;
arr[1].value=e[i].aname+"+"+e[i].aphone;
arr[2].value=e[i].address;
arr[3].value=e[i].mtime;
arr[4]=e[i].price;
if(e[i].uid1==that.uid){
arr[5]="buy"
}else if(e[i].uid2==that.uid){
arr[5]="sell"
}
that.list.push(arr)
}
})
},
data(){
return {
list: [],
money:"",
show5:false,
confirmTitle:"",
confirmMsg:"",
showTip:false,
tipMsg:"",
uid:localStorage.getItem("userFront")
}
},
methods:{
changeMoney(){

this.confirmTitle="充值金额"

this.confirmMsg=100.00

this.show5=true;
},
onConfirm5(value){
var that=this;
fetch("/users/addMoney?money="+value+"&uid="+that.uid).then(function (e) {
return e.text()
}).then(function (e) {
if(e=="ok"){
that.showTip=true;
that.tipMsg="充值成功";
that.money=parseFloat(that.money)+parseFloat(value);
}
})
},
onShow5(){

this.$refs.confirm5.setInputValue(this.confirmMsg)
}
}
}



