
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: "my-address",
components:{Toast},
data:function () {
return{
data:[],
show:false,
tipMsg:"",
uid:localStorage.getItem("userFront")
}
},
mounted(){
var that=this;
this.$emit('title',this.$route.query.title);
fetch("/goods/selectAddr/"+this.uid).then(function (e) {
return e.json()
}).then(function (e) {
that.data=e;
})
},
methods:{
choose(item){
this.$emit('chooseaddr',item);

this.$router.go(-1)
},
editAddr(aid){
this.stop();
this.$router.push({path:'/editAddress',query:{title:'管理收货地址',aid:aid}})
},
setDefault(aid){
this.stop();
var that=this;
fetch("/goods/setDefault?aid="+aid+"&uid="+that.uid).then(function (e) {
return e.json();
}).then(function (e) {
if(e.message){
that.show=true;
that.tipMsg="设置成功"
for(var i in that.data){
that.data[i].defau="1"
if(that.data[i].aid==aid){
that.data[i].defau="0"
}
}
}else{
that.show=true;
that.tipMsg="设置失败"
}
})
},
stop(e){
var evt = e ? e : window.event;
if (evt.stopPropagation) {        //W3C
evt.stopPropagation();
}else {       //IE
evt.stop = true;
}
}
}
}



