
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: "published",
data:function () {
return {
data:[],
uid:localStorage.getItem("userFront")
}
},
components:{Confirm,Toast},
mounted:function () {
var that=this;
this.$emit('title',this.$route.query.title);
fetch("/goods/buyed/"+this.uid).then(function (e) {
return e.json()
}).then(function (e) {
if(e.err){
that.showTip=true;
that.tipMsg="数据错误";
}else{
that.data=e;
for(var i in e){
var date=new Date(e[i].mtime)
e[i].mtime=date.toLocaleString();
}
}
})
},
methods:{

}
}



