
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
directives: {
TransferDom
},
props:["addrData"],
name: "buy",
components: {
XHeader,
Group,
Cell,
Popup,XSwitch,XButton,Toast
},
data:function () {
return {
showBuy:false,
gid:"",
gData:[],
imgArr:[],
showTip:false,
tipMessage:"",
address:"",
aid:"",
payAccount:"",
uid:localStorage.getItem("userFront")
}
},
mounted(){
this.gid=this.$route.params.gid;

var that=this;

this.payAccount=localStorage.getItem("userName")

fetch("/goods/query?gid="+this.gid+"&uid="+this.uid).then(function (e) {
return e.json()
}).then(function (e) {
that.gData=e[0];
if(e[0].gphoto){
that.imgArr=e[0].gphoto.split(";")
}
})
if(this.addrData.address){
this.address=this.addrData.aname+" "+this.addrData.aphone+" "+this.addrData.address;
this.aid=this.addrData.aid;
}else{

fetch("/goods/queryAddr/"+that.uid).then(function (e) {
return e.json();
}).then(function (e) {
that.address=e.aname+" "+e.aphone+" "+e.address;
that.aid=e.aid;
})
}
},
methods:{
showBuyDiv(){
if(this.aid){
this.showBuy =true;
}else{
this.showTip=true;this.tipMessage="地址不能为空"
}
},
buy(){
var that=this;
fetch("/goods/buy?gid="+this.gid+"&uid="+that.uid+"&price="+this.gData.price+"&aid="+this.aid).then(function (e) {
return e.json()
}).then(function (e) {
that.showBuy=false;
if(e.ok){
setTimeout(function () {
that.$router.push({path:"/buyed",query:{title:"我购买的"}})
},600)
}
that.showTip=true;that.tipMessage=e.msg
})
},
}
}



