
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: "edit-address",
components:{Group,XTextarea,XAddress,XInput,Toast},
data:function () {
return {
name:"",
phone:"",
addr:"",
showTip:false,
tipMessage:"",
showWarn:false,
warnMessage:"",
aid:"",
uid:localStorage.getItem("userFront")
}
},
mounted(){
this.$emit('title',this.$route.query.title);
this.aid=this.$route.query.aid;
var that=this;
if(this.aid){
fetch("/goods/selectEditAddr/"+this.aid).then(function (e) {
return e.json()
}).then(function (e) {
that.name=e[0].aname;
that.phone=e[0].aphone;
that.addr=e[0].address;
})
}
},
methods:{
add(){
var that=this;

if (!this.$refs.phone.valid ||this.phone.trim()==""){
this.showWarn=true;
this.warnMessage="联系方式不能为空";
return ;
}else if(this.name.trim()==""){
this.showWarn=true;
this.warnMessage="姓名不能为空";
return ;
}else if(this.addr.trim()==""){
this.showWarn=true;
this.warnMessage="详细地址不能为空";
return ;
}else{
if(this.aid){
fetch("/goods/editAddr",{
method: "POST",
headers: {
'Content-Type': 'application/x-www-form-urlencoded'
},
body: "aname="+that.name+"&aphone="+that.phone+"&address="+that.addr+"&aid="+this.aid
}).then(function (e) {
return e.json();
}).then(function (e) {
if(!e.message){
that.showWarn=true;
that.warnMessage="修改出现错误";
}else{
that.showTip=true;
that.tipMessage="修改成功";
setTimeout(function () {
that.$router.go(-1)
},600)
}
})
}else{
fetch("/goods/addAddr",{
method: "POST",
headers: {
'Content-Type': 'application/x-www-form-urlencoded'
},
body: "aname="+that.name+"&aphone="+that.phone+"&address="+that.addr+"&uid="+that.uid
}).then(function (e) {
return e.json();
}).then(function (e) {
if(!e.message){
that.showWarn=true;
that.warnMessage="添加出现错误";
}else{
that.showTip=true;
that.tipMessage="添加成功";
setTimeout(function () {
that.$router.go(-1)
},600)
}
})
}
}

},
del(aid){
var that=this;
fetch("/goods/delAddr/"+aid).then(function (e) {
return e.json();
}).then(function (e) {
if(!e.message){
that.showWarn=true;
that.warnMessage="删除出现错误";
}else{
that.showTip=true;
that.tipMessage="删除成功";
setTimeout(function () {
that.$router.go(-1)
},600)
}
})
}
}
}



