
import Scroller from 'vux/src/components/scroller/index.vue'
import Search from 'vux/src/components/search/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: "home",
components: {
Scroller,Search,LoadMore
},
/***
* goods
*   gid   name  time  title  info   img  price  category
* @returns {{goodsData: *[]}}
*/
data() {
return {
results: [],
value: '',
counter: 0, //当前页
num: 10, // 一页显示多少条
goodsData: [], // 下拉更新数据存放数组

onFetching: false,
bottomCount: 20,
haveData:true

}
},
mounted:function () {
var that=this;
fetch("/goods/select?counter="+this.counter+"&num="+that.num).then(function (e) {
return e.json()
}).then(function (e) {
if(e.length){
that.goodsData=e;
that.$refs.scrollerBottom.reset()
}else {
that.haveData=false;
that.$refs.scrollerBottom.reset()
}
})
},
methods: {
onPulldown(){
this.haveData=true
if(this.onFetching){

}else{
this.onFetching = true
this.counter++;
fetch("/goods/select?num="+this.num+"&counter="+this.counter).then((e)=>{
return e.json();
}).then((e)=>{
if(e.length){
this.goodsData=e.concat(this.goodsData)
this.$nextTick(() => {
this.$refs.scrollerBottom.donePulldown()
})
}else {
this.counter--;
this.haveData=false;
this.$nextTick(() => {
this.$refs.scrollerBottom.donePulldown()
})
document.querySelector(".noDataTip").style.display="block"
setTimeout(()=>{
document.querySelector(".noDataTip").style.display="none"
},2000)
}
this.onFetching = false;
})
}
},
onScrollBottom () {
this.haveData=true;
if (this.onFetching) {

} else {
this.onFetching = true
this.counter++;
fetch("/goods/select?num="+this.num+"&counter="+this.counter).then((e)=>{
return e.json();
}).then((e)=>{
if(e.length){
this.goodsData=this.goodsData.concat(e)
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
}else {
this.counter--;
this.haveData=false;
this.$refs.scrollerBottom.reset()

}
this.onFetching = false
})
}
},
touchStart: function (ev) {
this.startX = ev.touches[0].clientX;

var imgs = ev.target.parentNode.getElementsByTagName("img");
var width = 0;
for (var i = 0; i < imgs.length; i++) {
width += imgs[i].width;
width += 15;
}
let imgView = ev.target.previousElementSibling;
imgView.style.width = width + 'px';
let a=ev.target.parentNode.parentNode.parentNode;
let index=ev.target.getAttribute("index")
this.$refs.scroller[index].reset();
},

gotoInfo:function (gid) {
this.$router.push("/goodsInfo/"+gid)
},

resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},

}
}

function getResult (val) {
let rs = []
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}



