
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: "published",
data:function () {
return {
data:[],
uid:localStorage.getItem("userFront")
}
},
components:{Confirm,Toast},
mounted:function () {
var that=this;
this.$emit('title',this.$route.query.title);
fetch("/goods/collected/"+this.uid).then(function (e) {
return e.json()
}).then(function (e) {
if(e.err){
that.showTip=true;
that.tipMsg="数据错误";
}else{
that.data=e;
}
})
},
methods:{
delcollect(colid){
var that=this;
fetch("/goods/delCollect?colid="+colid).then(function (e) {
return e.text()
}).then(function (e) {
if(e=="ok"){
that.showTip=true;that.tipMsg="已取消收藏"
var lis=document.querySelectorAll("li");
for(var i=0;i<lis.length;i++){
if(lis[i].getAttribute("index")==colid){
document.querySelector("ul").removeChild(lis[i])
}
}
}
})
},
}
}



