
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: "login",
components:{
Toast
},
data:function () {
return {
account:"",
password:"",
password2:"",
showTip:false,
tipMessage:"",
showWarn:false,
warnMessage:"",
may:false
}
},
methods:{
register(){
var that=this;
if(this.may){
if(this.password!=this.password2){
that.showWarn=true;
that.warnMessage="两次密码不一致";
return ;
}
fetch("/users/register",{
method: "POST",
headers: {
'Content-Type': 'application/x-www-form-urlencoded'
},
body: "account="+that.account+"&password="+that.password+"&password2="+that.password2
}).then(function (e) {
return e.json();
}).then(function (e) {
localStorage.userFront=e[0].uid;
localStorage.userName=e[0].name;
localStorage.userPhone=e[0].phone;
that.showTip=true;
that.tipMessage="注册成功";
setTimeout(function () {
that.$router.go(-2)
},600)
})
}else{
that.showWarn=true;
that.warnMessage="请正确填写信息";
}

},
accountBlur(){
var that=this;
var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
if(this.account==''){
that.showWarn=true;
that.warnMessage="请输入手机号";
}else if(!reg.test(this.account)){
that.showWarn=true;
that.warnMessage="手机格式不正确";
}else{
fetch("/users/selected/"+this.account).then(function (e) {
return e.text()
}).then(function (e) {
if(e=="already"){
that.showWarn=true;
that.warnMessage="该手机号已注册过";
that.may=false;
}else if(e=="none"){
that.may=true;
}
})
}
},
login(){
this.$router.go(-1)
}
}
}



