
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
directives: {
TransferDom
},
name: "published",
data:function () {
return {
show:false,
delId:"",
showTip:false,
tipMsg:"",
data:[],
uid:localStorage.getItem("userFront")
}
},
components:{Confirm,Toast,Tab,TabItem},
mounted:function () {
var that=this;
this.$emit('title',this.$route.query.title);
fetch("/users/published?uid="+this.uid+"&state=1").then(function (e) {
return e.json()
}).then(function (e) {
if(e.err){
that.showTip=true;
that.tipMsg="数据错误";
}else{
that.data=e;
}
})
},
methods:{
edit(gid){
this.$router.push("/publish/"+gid)
},
del(gid){
this.delId=gid;
this.show=true;
},
onConfirm () {
var that=this;
fetch("/goods/del/"+this.delId).then(function (e) {
return e.text();
}).then(function (e) {
if(e=="ok"){
var lis=document.querySelectorAll("li")
for(var i=0;i<lis.length;i++){
if(lis[i].getAttribute("index")==that.delId){
document.querySelector("ul").removeChild(lis[i])
that.showTip=true;
that.tipMsg="删除成功";
}
}
}else{
that.showTip=true;
that.tipMsg="删除失败";
}
})
},
onItemClick (index) {
if(index==0){
this.$emit('title',this.$route.query.title);
fetch("/users/published?uid="+this.uid+"&state=1").then((e)=> {
return e.json()
}).then((e)=> {
if(e.err){
this.showTip=true;
this.tipMsg="数据错误";
}else{
this.data=e;
}
})
}else if(index==1){

this.$emit('title',this.$route.query.title);
fetch("/users/published?uid="+this.uid+"&state=0").then((e)=> {
return e.json()
}).then((e)=> {
if(e.err){
this.showTip=true;
this.tipMsg="数据错误";
}else{
this.data=e;
}
})
}else if(index == 2){
this.$emit('title',this.$route.query.title);
fetch("/users/published?uid="+this.uid+"&state=2").then((e)=> {
return e.json()
}).then((e)=> {
if(e.err){
this.showTip=true;
this.tipMsg="数据错误";
}else{
this.data=e;
}
})
}
},
}
}



