
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "set-main",
directives: {
TransferDom
},
data:function () {
return {
show:false,
showTip:"",

show5:false,
userInfo:[],
confirmTitle:"",
confirmMsg:"",
confirmType:"",
may:false,
uid:localStorage.getItem("userFront")
}
},
components:{
XButton,Group,Cell,XHeader,Toast,XInput,Confirm
},
mounted:function () {
this.$emit('title',this.$route.query.title);
var that=this;
fetch("/users/select/"+that.uid).then(function (e) {
return e.json()
}).then(function (e) {
that.userInfo=e[0]
})
},
methods:{
out(){
var that=this;
localStorage.removeItem("userFront");
localStorage.removeItem("userName")
localStorage.removeItem("userPhone")
this.show=true;
this.showTip="退出成功！";
setTimeout(function () {
that.$router.push("/")
},500)
},
onShow5 () {
this.$refs.confirm5.setInputValue(this.confirmMsg)
},
onConfirm5 (value) {
var that=this;
if(this.confirmType=="name"){
if(value.trim()==""){
this.show=true;
this.showTip="用户名不能为空";
}else{
this.may=true;
}
}else if(this.confirmType=="phone"){
if(value.trim()==""){
this.show=true;
this.showTip="手机号不能为空";
}else if(!(/^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\\d{8}$/.test(value))){
this.show=true;
this.showTip="手机号格式错误";
}else{
this.may=true;
}
}
if(this.may){
fetch("/users/update?type="+that.confirmType+"&a="+value+"&uid="+that.userInfo.uid).then(function (e) {
return e.text()
}).then(function (e) {
if(e=="ok"){
if(that.confirmType=="name"){
that.userInfo.name=value;
that.may=false;
}else if(that.confirmType=="phone"){
that.userInfo.phone=value;
that.may=false;
}
}
})
}

},
changeAccount(){
this.confirmType="name"
this.confirmTitle="修改用户名"
this.confirmMsg=this.userInfo.name
this.show5=true;
},
changePhone(){
this.confirmType="phone"
this.confirmTitle="修改手机号"
this.confirmMsg=this.userInfo.phone
this.show5=true;
},

gotoSetPass(){
this.$router.push({path:'/setPass',query:{title:'修改密码'}})
}
}
}



