
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import photoUpload from "@/assets/js/photoUpload"
export default {
name: "personal",
components: {
Scroller,XHeader,Toast,
Divider,
Spinner,
XButton,
Group,
Cell,
LoadMore, ButtonTab, ButtonTabItem
},
data:function () {
return {
userFront:false,
btnTab:0,
name:"",
photo:"",
showTip:false,
tipMsg:"",
uid:localStorage.getItem("userFront")
}
},
mounted:function () {
var that=this;
if(this.uid){
this.userFront=true;
fetch("/users/select/"+that.uid).then(function (e) {
return e.json()
}).then(function (e) {
that.name=e[0].name;
that.photo=e[0].photo;
})
}
},
methods:{
gotoSet(){
this.$router.push({path:"/setMain",query:{title:"设置"}})
},
changePhoto(){

var that=this;
var parent=document.querySelector(".maskImg div");
var inputFile=document.querySelector("input")
photoUpload.uploadInit(parent,inputFile);
inputFile.addEventListener("change",function () {
photoUpload.up("/goods/upload",function (e) {

fetch("/users/updatePhoto?photo="+e+"&uid="+that.uid).then(function (e) {
return e.text()
}).then(function (e) {
if(e=="ok"){
that.showTip=true;that.tipMsg="修改成功"
}
})
})
})
}
}
}



